import { applyMiddleware, createStore } from 'redux';
import thunk from 'redux-thunk';
import { factory as directionsAPI } from '../../sdk/directions';
import { factory as geocoderAPI } from '../../sdk/geocoder';
import { factory as leisureAPI } from '../../sdk/leisure';
import { defaultState as defaultItineraryState } from '../itinerary/reducer';
import { defaultState as defaultSettingsState } from '../settings/reducer';
import { defaultState as defaultSearchState } from '../search/reducers';
import { defaultState as defaultMapState } from '../map/reducer';
import { defaultState as defaultLeisureState } from '../leisure/reducer';
import apiConf from '../../conf/api';
import { defaultState as defaultNavigationState, reducer as navigationReducer } from '../navigation/reducer';
import { createReducer } from './combine-reducer';
const debugMiddleware = store => next => action => {
    console.log(action);
    return next(action);
};
export const defaultState = () => ({
    navigation: defaultNavigationState(),
    itinerary: defaultItineraryState(),
    search: defaultSearchState(),
    settings: defaultSettingsState(),
    map: defaultMapState(),
    leisure: defaultLeisureState()
});
const defaultAPI = {
    directions: directionsAPI({
        endpoint: apiConf.endpoint
    }),
    geocoder: geocoderAPI({
        endpoint: apiConf.endpoint
    }),
    leisure: leisureAPI({
        endpoint: apiConf.endpoint
    })
};
// placeholder for dynamically injected reducers
const passThroughReducer = (state = {}) => state;
export const store = (api = defaultAPI) => (initialState = defaultState()) => {
    const staticReducer = {
        navigation: navigationReducer,
        map: passThroughReducer,
        itinerary: passThroughReducer,
        settings: passThroughReducer,
        search: passThroughReducer,
        leisure: passThroughReducer
    };
    // @ts-ignore
    const store = createStore(createReducer(staticReducer), initialState, applyMiddleware(thunk.withExtraArgument(api)));
    const dynamicReducers = {};
    // @ts-ignore
    return Object.assign(store, {
        injectReducer(key, reducer) {
            dynamicReducers[key] = reducer;
            store.replaceReducer(createReducer(Object.assign({}, staticReducer, dynamicReducers)));
        }
    });
};
