export const EMPTY_SOURCE = Object.freeze({
    type: 'geojson',
    data: {
        type: 'FeatureCollection',
        features: []
    }
});
export const eventuallyUpdate = map => (layer, slicer, updateFunction) => {
    let currentState = null;
    return (state) => {
        let updated = false;
        const newState = slicer(state);
        if (currentState !== newState) {
            map.getSource(layer).setData(updateFunction(newState));
            updated = true;
        }
        currentState = newState;
        return updated;
    };
};
export const pointListToFeature = (points) => ({
    type: 'FeatureCollection',
    features: points.map(pointToFeature)
});
const pointToFeature = (point) => ({
    type: 'Feature',
    geometry: {
        type: 'Point',
        coordinates: [point.lng, point.lat]
    }
});
