import { ActionType } from '../common/actions';
import { createSearchResultInstance } from '../search/elements/search-result';
const hasValue = p => p.item !== null;
export const addItineraryPoint = (point, beforeId = null) => ({
    type: ActionType.ADD_ITINERARY_POINT,
    point,
    beforeId
});
export const removeItineraryPoint = (id) => ({
    type: ActionType.REMOVE_ITINERARY_POINT,
    id
});
export const updateItineraryPoint = (id, location) => ({
    type: ActionType.UPDATE_ITINERARY_POINT,
    id,
    location
});
export const goTo = (location) => ({
    type: ActionType.GO_TO,
    location
});
export const goFrom = (location) => ({
    type: ActionType.GO_FROM,
    location
});
export const fetchRoutes = () => ({
    type: ActionType.FETCH_ROUTES
});
export const fetchRoutesWithSuccess = (routes) => ({
    type: ActionType.FETCH_ROUTES_SUCCESS,
    routes
});
export const selectRoute = (route) => ({
    type: ActionType.SELECT_ROUTE,
    route
});
export const fetchRoutesWithFailure = (error) => ({
    type: ActionType.FETCH_ROUTES_FAILURE,
    error
});
export const resetRoutes = () => ({
    type: ActionType.RESET_ROUTES
});
// thunks to handle side effects of stop points change
const eventuallyUpdateRoutes = (actionCreator) => (...args) => async (dispatch, getState) => {
    dispatch(actionCreator(...args));
    const stops = getState()
        .itinerary
        .stops
        .filter(hasValue);
    if (stops.length >= 2) {
        return dispatch(fetchRoutesFromAPI());
    }
};
export var InsertionPosition;
(function (InsertionPosition) {
    InsertionPosition["BEFORE"] = "BEFORE";
    InsertionPosition["AFTER"] = "AFTER";
})(InsertionPosition || (InsertionPosition = {}));
export const fetchRoutesFromAPI = () => async (dispatch, getState, API) => {
    const { directions } = API;
    dispatch(fetchRoutes());
    try {
        const { stops } = getState()
            .itinerary;
        const points = stops
            .filter(hasValue)
            .map(({ item }) => createSearchResultInstance(item).toPoint());
        const res = await directions.search(points);
        return dispatch(fetchRoutesWithSuccess(res));
    }
    catch (e) {
        return dispatch(fetchRoutesWithFailure(e));
    }
};
export const moveItineraryPoint = (sourceId, targetId, position) => ({
    type: ActionType.MOVE_ITINERARY_POINT,
    sourceId,
    targetId,
    position
});
export const addItineraryPointWithSideEffects = eventuallyUpdateRoutes(addItineraryPoint);
export const removeItineraryPointWithSideEffects = eventuallyUpdateRoutes(removeItineraryPoint);
export const changeItineraryPointWithSideEffects = eventuallyUpdateRoutes(updateItineraryPoint);
export const moveItineraryPointWithSideEffects = eventuallyUpdateRoutes(moveItineraryPoint);
